
html, body {
  height: 100%;
}
body {
  margin: 0;
  font-size: 16px;
  line-height: 1.4;
  font-family: Helvetica, sans-serif;
}
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: #4fc08d;
}
a:hover {
  text-decoration: underline;
}



